import { fetch } from 'src/lib/fetch.js'

export default {
  // 查询个人资料
  self (params) {
    return fetch('/pi/bizuser/self', params)
  },
  // 充值
  recharge (params) {
    return fetch('/pi/bizorder/recharge', params)
  },
  // check充值
  rechargeCheck (params) {
    return fetch(`/pi/bizorder/check/${params.orderNo}`, params)
  }
}



